import React from "react";
import { useDispatch, useSelector } from "react-redux";
import { btnTodo, updateTodos } from "../../../redux/reducers/reducerTodo";
import DeletedTodo from "../deletedTodo/deletedTodo";
import Todo from "../todo";
import TodoBg from "../todo-bg";
import "./todolist.css";

const TodoList = () => {
  const dispatch = useDispatch();

  const searchInput = useSelector((s) => s.reducerTodo.searchInput);
  const todos = useSelector((s) => s.reducerTodo.todos);
  const statusBtn = useSelector((s) => s.reducerTodo.statusBtn);
  const activeTodos = [...todos].filter((todo) => {
    return !todo.isDeleted;
  });
  const deleteAllHandler = () => {
    dispatch(btnTodo(todos.filter((todo) => !todo.isDeleted)));
  };
  return (
    <ul className="todolist" id="todoList">
      {todos ? (
        !todos.length ? (
          <TodoBg />
        ) : (
          todos
            .filter((todo) => {
              if (statusBtn === "all") {
                return todo && !todo.isDeleted;
              }
              if (statusBtn === "active") {
                return todo.isActive && !todo.isDeleted;
              }
              if (statusBtn === "done") {
                return !todo.isActive && !todo.isDeleted;
              }
              if (statusBtn === "deleted") {
                return todo.isDeleted;
              }
            })
            .filter((todo) => {
              return todo.todoName.includes(searchInput);
            })
            .map((todo) => {
              if (statusBtn === "deleted") {
                return (
                  <div>
                    <DeletedTodo
                      todoObj={todo}
                      name={todo.todoName}
                      key={todo.id}
                    />
                    <button
                    title='Empty Trash'
                      className="confirm-btn fas fa-recycle"
                      type="button"
                      onClick={deleteAllHandler}
                    ></button>
                  </div>
                );
              } else {
                return (
                  <Todo todoObj={todo} name={todo.todoName} key={todo.id} />
                );
              }
            })
        )
      ) : (
        updateTodos([])
      )}{" "}
    </ul>
  );
};

export default TodoList;
