const UPDATE_TODOS = "UPDATE_TODOS";
const BTN_TODO = "BTN_TODO";
const STATUS_BTN = "STATUS_BTN";
const SEARCH_INPUT = "SEARCH_INPUT";
const initState = {
  todos: [],
  statusBtn: "all",
  searchInput: "",
  trashBtn: "",
};

export default (state = initState, action) => {
  switch (action.type) {
    case SEARCH_INPUT: {
      return {
        ...state,
        searchInput: action.searchInput,
      };
    }
    case BTN_TODO: {
      return {
        ...state,
        todos: action.todos,
      };
    }
    case STATUS_BTN: {
      return {
        ...state,
        statusBtn: action.statusBtn,
      };
    }
    case UPDATE_TODOS: {
      return {
        ...state,
        todos: [action.todo, ...state.todos],
      };
    }
    case "EMPTY_TODO": {
      return {
        ...state,
        todos: action.todo,
      };
    }
    default: {
      return state;
    }
  }
};
export const emptyTodo = (newArr) => {
  return {
    type: "EMPTY_TODO",
    todo: newArr,
  };
};

export const updateTodos = (inputText) => {
  return {
    type: UPDATE_TODOS,
    todo: {
      todoName: inputText,
      id: Math.round(Math.random() * 1000),
      isActive: true,
      isImportant: false,
      isDeleted: false,
    },
  };
};
export const btnTodo = (newArr) => {
  return {
    type: BTN_TODO,
    todos: newArr,
  };
};
export const setStatusBtn = (newArr) => {
  return {
    type: STATUS_BTN,
    statusBtn: newArr,
  };
};
export const setSearchInput = (newArr) => {
  return {
    type: SEARCH_INPUT,
    searchInput: newArr,
  };
};
