import React from "react";
import { useDispatch, useSelector } from "react-redux";
import { btnTodo } from "../../../redux/reducers/reducerTodo";
import "./todo.css";

const Todo = ({ name, todoObj }) => {
  const dispatch = useDispatch();
  const todos = useSelector((s) => s.reducerTodo.todos);
  const doneHandler = () => {
    dispatch(
      btnTodo(
        todos.map((todo) =>
          todo.id === todoObj.id ? { ...todo, isActive: !todo.isActive } : todo
        )
      )
    );
  };
  const importantHandler = () => {
    dispatch(
      btnTodo(
        todos.map((todo) =>
          todo.id === todoObj.id
            ? { ...todo, isImportant: !todo.isImportant }
            : todo
        )
      )
    );
  };
  const deleteHandler = () => {
    dispatch(
      btnTodo(
        todos.map((todo) =>
          todo.id === todoObj.id
            ? { ...todo, isDeleted: !todo.isDeleted }
            : todo
        )
      )
    );
  };

  return (
    <li className={`todo-item ${todoObj.isDeleted ? "delete" : ""}`}>
      <p
        className={`todo-text ${todoObj.isActive ? "" : "done"} ${
          todoObj.isImportant ? "important" : ""
        }`}
      >
        {name}
      </p>
      <div className="todo-btns">
        <button
          title="Mark as Completed"
          className={`todo-btn fas fa-check ${
            todoObj.isActive ? "" : "active"
          }`}
          type="button"
          onClick={doneHandler}
        ></button>
        <button
          title="Mark as Important"
          className={`todo-btn fas fa-star ${
            todoObj.isImportant ? "active" : ""
          }`}
          type="button"
          onClick={importantHandler}
        ></button>
        <button
          title="Move To Trash"
          className={`todo-btn fas fa-trash ${
            todoObj.isDeleted ? "active" : ""
          }`}
          type="button"
          onClick={deleteHandler}
        ></button>
      </div>
    </li>
  );
};

export default Todo;
