import React from "react";
import { useDispatch, useSelector } from "react-redux";
import { setStatusBtn, updateTodos } from "../../redux/reducers/reducerTodo";
import "../tasks/todo/todo.css";
import "./newTodo.css";

const NewTodo = (todoObj) => {
  const dispatch = useDispatch();
  const statusBtn = useSelector((s) => s.reducerTodo.statusBtn);
  const trashBtnHandler = (e) => {
    dispatch(setStatusBtn(e.target.value));
  };
  const formHandler = (e) => {
    e.preventDefault();
    if (e.target.children[0].value !== "") {
      dispatch(updateTodos(e.target.children[0].value));
    }
    e.target.children[0].value = "";
  };
  return (
    <form className="newtodo-container" onSubmit={formHandler}>
      <input
        autocomplete="off"
        className="newtodo-box"
        id="newtodo-box"
        type="text"
        placeholder="New To-Do"
      />
      <label htmlFor="newtodo-box" className="newtodo-label">
        <i class="newtodo-icon fas fa-plus" />
      </label>
      <button type="submit" className="newtodo-btn"></button>
      <button
        className={`trash-btn fas fa-trash ${
          statusBtn === "deleted" ? "active" : ""
        }`}
        value="deleted"
        type="reset"
        onClick={trashBtnHandler}
      ></button>
    </form>
  );
};

export default NewTodo;
