import React from "react";
import { useDispatch, useSelector } from "react-redux";
import { setStatusBtn } from "../../redux/reducers/reducerTodo";
import "./statusbtns.css";

const StatusBtns = () => {
  const dispatch = useDispatch();
  const statusBtn = useSelector((s) => s.reducerTodo.statusBtn);
  const statusBtnHandler = (e) => {
    dispatch(setStatusBtn(e.target.value));
  };
  return (
    <div className="status-btns">
      <button
        className={`status-btn ${statusBtn === "all" ? "active" : ""}`}
        value="all"
        type="button"
        onClick={statusBtnHandler}
      >
        All
      </button>
      <button
        className={`status-btn ${statusBtn === "active" ? "active" : ""}`}
        value="active"
        type="button"
        onClick={statusBtnHandler}
      >
        Active
      </button>
      <button
        className={`status-btn ${statusBtn === "done" ? "active" : ""}`}
        value="done"
        type="button"
        onClick={statusBtnHandler}
      >
        Done
      </button>
    </div>
  );
};

export default StatusBtns;
