import React from "react";
import { useDispatch, useSelector } from "react-redux";
import { btnTodo } from "../../../redux/reducers/reducerTodo";
import "./style.css";

const DeletedTodo = ({ name, todoObj }) => {
  const dispatch = useDispatch();
  const todos = useSelector((s) => s.reducerTodo.todos);
  const restoreHandler = () => {
    dispatch(
      btnTodo(
        todos.map((todo) =>
          todo.id === todoObj.id
            ? { ...todo, isDeleted: !todo.isDeleted }
            : todo
        )
      )
    );
  };
  const confirmDeleteHandler = () => {
    dispatch(btnTodo(todos.filter((todo) => todo.id !== todoObj.id)));
  };

  return (
    <li className={`todo-item ${todoObj.isDeleted ? "delete" : ""}`}>
      <p
        className={`todo-text ${todoObj.isActive ? "" : "done"} ${
          todoObj.isImportant ? "important" : ""
        }`}
      >
        {name}
      </p>
      <div className="deletedTodo-btns">
        <button
          title="restore"
          className={`deletedTodo-btn fas fa-trash-restore ${
            todoObj.isActive ? "" : "active"
          }`}
          type="button"
          onClick={restoreHandler}
        ></button>
        <button
          title="delete"
          className={`deletedTodo-btn fas fa-trash ${
            todoObj.isDeleted ? "active" : ""
          }`}
          type="button"
          onClick={confirmDeleteHandler}
        ></button>
      </div>
    </li>
  );
};

export default DeletedTodo;
